
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Search from 'vux/src/components/search/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import goodslist from './goods_list.vue'
import Scroller from '@/components/scroller_me/scroller';
import Loading from '@/components/loading/dataLoading'

const baseList = [{
  url: 'javascript:',
  img: 'http://pic.qiantucdn.com/58pic/14/59/28/74A58PICIrd_1024.jpg',
  title: 'fua'
}, {
  url: 'javascript:',
  img: 'http://pic.qiantucdn.com/58pic/23/12/64/92G58PIC93T_1024.jpg',
  title: ''
}, {
  url: 'javascript:',
  img: 'http://pic.qiantucdn.com/58pic/25/94/22/94558PICxJ3_1024.jpg',
  title: ''
}];

const goodList=[
  {
    goods_id:1,
    img:"http://pic.qiantucdn.com/58pic/25/94/22/94558PICxJ3_1024.jpg",
    name:"",
    price:1500,
    sale_num:200
  },{
    goods_id:2,
    img:"http://pic.qiantucdn.com/58pic/25/94/22/94558PICxJ3_1024.jpg",
    name:"",
    price:1500,
    sale_num:200
  },{
    goods_id:3,
    img:"http://pic.qiantucdn.com/58pic/25/94/22/94558PICxJ3_1024.jpg",
    name:"",
    price:1500,
    sale_num:200
  }
];
const goodsItem=goodList.map((item, index) => ({
  goods_id: item.goods_id,
  img: item.img,
  name: item.name,
  price: item.price,
  sale_num: item.sale_num,
}));
const urlList = baseList.map((item, index) => ({
  url: 'http://m.baidu.com',
  img: item.img,
  title: `()${item.title}`
}));

export default {
  data () {
    return {
      loading:true,
      demo06_list: urlList,//
      demo06_index: 0,
      results: [],//
      value: 'test',
      goodsItem:goodsItem,//
      current_sort:'',
      sort:[],
      show_sort:false,
      show_rack:false,
      current_rack:'',
      rack:[],
      pageno:0,
      pagesize:4,
      current_sort_key:0,
      current_rack_key:0,
      totalpage:[]
    }
  },
  components: {
    Swiper,Search,goodslist,Selector, Group,Actionsheet,Scroller,Loading
  },
  mounted(){
    this.getlist();
  },
  methods: {
    getlist(){
        const self = this;
        const senddata = {
          "data": {
            "pageno":this.pageno,
            "pagesize":this.pagesize,
            "sort":this.current_sort_key,
            "rack":this.current_rack_key
          }
        };
        this.$http.post(service_url+'/o2o/shop/wx/indexinfo.do',senddata).then( (data)=> {
          /*if(mark){
            let goodsList = data.body.fields.goodsList;
            for(let i =0;i<goodsList.length;i++){
              self.goodsItem.push(goodsList[i]);
            }

          }else{
            self.goodsItem=data.body.fields.goodsList;
          }*/
          self.totalpage=data.body.totalpage;
          self.goodsItem=data.body.fields.goodsList;

          let arr_sort=data.body.fields.sort;
          let json_sort={};
          for(var i=0;i<arr_sort.length;i++)
          {
            json_sort[arr_sort[i].key]=arr_sort[i].value
          }
          self.sort=json_sort;

          let arr_rack=data.body.fields.rack;
          let json_rack={};
          for(var j=0;j<arr_rack.length;j++)
          {
            json_rack[arr_rack[j].key]=arr_rack[j].value
          }
          self.rack=json_rack;

        },()=>{
          console.log(2);
        });
        this.loading=false;
      },
    demo06_onIndexChange (index) {
      this.demo06_index = index
    },
    resultClick (item) {
      window.alert('you click the result item: ' + JSON.stringify(item))
    },
    getResult (val) {
      this.results = val ? getResult(this.value) : []
    },
    onFocus () {
      console.log('on focus')
    },
    onCancel () {
      console.log('on cancel')
    },
    showActionsheet () {
      this.show_sort=true;
    },
    showActionsheet2 () {
      this.show_rack=true;
    },
    click_sort(key){
      this.pageno=0;
      this.current_sort_key=parseInt(key);
      this.getlist();
      for(let i in this.sort){
        if(i==key){        //item Json'name'
          let jValue=this.sort[i];//keyvalue
          this.current_sort=jValue;
        }
      }
    },
    click_rack(key){
      this.pageno=0;
      this.current_rack_key=parseInt(key);
      this.getlist();
      for(let i in this.rack){
        if(i==key){        //item Json'name'
          let jValue=this.rack[i];//keyvalue
          this.current_rack=jValue;
        }
      }
    },
    onInfinite(done) {
      console.log("onInfinite")
      this.pageno++;
      const senddata = {
        "data": {
          "pageno":this.pageno,
          "pagesize":this.pagesize,
          "sort":this.current_sort_key,
          "rack":this.current_rack_key
        }
      };
      const self = this;
      if(this.pageno>this.totalpage-1){
        this.$el.querySelector('.load-more').style.display = 'none';
        return;
      }else{
        this.$http.post(service_url+'/o2o/shop/wx/indexinfo.do',senddata).then( (data)=> {
          self.totalpage=data.body.totalpage;
          let goodsList = data.body.fields.goodsList;
          for(let i =0;i<goodsList.length;i++){
            self.goodsItem.push(goodsList[i]);
          }
          done();
        })
      }
    },
    onRefresh(done) {
      this.pageno=0;
      console.log("onRefresh")
      this.getlist();

      done() // call done
    },
  },

}
function getResult (val) {
  let rs = []
  for (let i = 0; i < 8; i++) {
    rs.push({
      title: `${val} result: ${i + 1} `,
      other: i
    })
  }
  return rs
}



